import 'bootstrap/dist/css/bootstrap.min.css';
import { useEffect, useState } from 'react';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import { InfoCircle } from 'react-bootstrap-icons';

//styles
import PageList from '../PageList';
import * as S from './styles';
import { MenuContainer, SidenavMenuList } from './styles';
import { getFromSessionStorage } from '../../constants/functions';
import { useNavbarContext } from '../../context/NavbarContext';

interface SidenavProps {
  minimize: boolean;
}
let firstLoad = true;
const Sidenav = () => {
  const [hideLabel, setHideLabel] = useState<boolean>(true);

  const [_minimize, setMinize] = useState(!!getFromSessionStorage('minimize'));
  const [style, setStyle] = useState<any>({
    minWidth: '244px',
    transition: 'all .2s linear',
  });
  const { openMenu } = useNavbarContext();

  useEffect(() => {
    setMinize(openMenu);
    if (!openMenu) {
      if (firstLoad) {
        setTimeout(() => {
          firstLoad = false;
        }, 200);
      } else {
        setTimeout(() => setHideLabel(openMenu), 200);
      }
    } else {
      setHideLabel(openMenu);
    }
    setStyle({
      minWidth: !openMenu ? '244px' : '88px',
      transition: 'all .2s linear',
    });
  }, [openMenu]);

  return (
    <S.SidenavContainer style={style}>
      <MenuContainer>
        <SidenavMenuList>
          <PageList minimize={_minimize} hideLabel={hideLabel}></PageList>
        </SidenavMenuList>
      </MenuContainer>

      <OverlayTrigger
        placement="right"
        overlay={
          _minimize ? (
            <Tooltip id="tooltip" style={{ marginLeft: '15px' }}>
              Para outras informaes, utilize nosso atendimento atravs do telefone (41) 30310-8000
            </Tooltip>
          ) : (
            <></>
          )
        }
      >
        <S.SidenavMenuListItemDesktop active={false} minimize={_minimize}>
          {_minimize ? (
            <S.IconContainer>
              <InfoCircle color="#fff" />
            </S.IconContainer>
          ) : (
            <></>
          )}
          {!hideLabel && (
            <S.SmallInfo>
              Para outras informaes, utilize nosso atendimento atravs do telefone (41) 30310-8000
            </S.SmallInfo>
          )}
        </S.SidenavMenuListItemDesktop>
      </OverlayTrigger>
    </S.SidenavContainer>
  );
};

export default Sidenav;
