  '21': string;
  '22': string;
  '23': string;
  '24': string;
  statusRetorno: string;
  consorciadoGruposCotasSegmentos: string;
  consorciadoCotaDigito: string;
  usuarioNome: string;
  consorciadoUltimoAcesso: string;
