import { useState } from 'react';
import { Form } from 'react-bootstrap';
import { Input } from '../../components/Form/Input/Input';

//styles
import { useNavigate } from 'react-router-dom';
import ModalInfo from '../../components/ModalInfo';
import { useUserContext } from '../../context/UserContext';
import { useStylization } from '../../hooks/useStylization';
import * as S from './styles';

const Login = () => {
  const stylization = useStylization();

  const [group, setGroup] = useState<string>('00175');
  const [quota, setQuota] = useState<string>('002');
  const [errorMessage, setErrorMessage] = useState<string>('');
  const [password, setPassword] = useState<string>('2549');
  const [digit, setDigit] = useState<string>('00');

  const navigate = useNavigate();
  const [show, setShow] = useState(false);
  const [validated, setValidated] = useState<boolean>(false);

  const { makeLogin } = useUserContext();

  const handleSubmit = async (event: any) => {
    event.preventDefault();
    event.stopPropagation();

    const form = event.currentTarget;
    if (form.checkValidity() === true) {
      try {
        const res = await makeLogin({ group, quota, password, digit });
        if (res.error) {
          setErrorMessage(res.message);
          return handleShow();
        }
        navigate('/disponibilidade-dos-grupos');
      } catch (e: any) {
        console.log(e);
      }
    }

    setValidated(true);
  };

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  return (
    <S.Content loginbackground={stylization?.imagemFundo + ''}>
      <div className="container">
        <div className="row">
          <div className="col-lg-6 ms-auto d-flex align-items-center justify-content-center">
            <S.Box>
              <div>
                <figure className="text-center mx-auto logoSecondary">
                  <img src={stylization?.logoPrincipal} alt="" className="w-100" />
                </figure>

                <ModalInfo
                  message={errorMessage}
                  show={show}
                  handleClose={handleClose}
                  header={'Acesso Negado'}
                ></ModalInfo>

                <S.Head>
                  <h1>{stylization?.tituloPrincipal}</h1>
                  <p>{stylization?.subTitulo}</p>
                </S.Head>

                {group === '' || quota === '' || password === '' ? (
                  <S.AlertDanger>Preencha todos os campos!</S.AlertDanger>
                ) : null}

                <Form noValidate validated={validated} onSubmit={handleSubmit}>
                  <Input
                    type="text"
                    id="group"
                    placeholder="Grupo"
                    required={true}
                    onChange={(e: any) => setGroup(e.target.value)}
                    value={group}
                  />

                  <div className="row">
                    <div className={'col-md-6'}>
                      <Input
                        type="text"
                        id="quota"
                        required={true}
                        placeholder="Cota"
                        onChange={(e: any) => setQuota(e.target.value)}
                        value={quota}
                      />
                    </div>
                    <div className={'col-md-6'}>
                      <Input
                        type="text"
                        id="digito"
                        placeholder="Digito"
                        onChange={(e: any) => setDigit(e.target.value)}
                        value={digit}
                      />
                    </div>
                  </div>

                  <Input
                    type="password"
                    id="password"
                    required={true}
                    placeholder="Senha"
                    value={password}
                    onChange={(e: any) => setPassword(e.target.value)}
                  />
                  <S.Submit type="submit">Fazer login</S.Submit>
                </Form>

                <div className="text-center">
                  <S.NavigatePassword to="/recuperar-senha" title="Recuperar Senha">
                    Esqueci minha senha
                  </S.NavigatePassword>
                </div>
              </div>

              <div className="infoFooter">
                <p className="text-center mb-0">{stylization?.textoRodape}</p>

                <div className="text-center d-flex justify-content-center align-items-center gap-2 flex-wrap">
                  <strong>{stylization?.textoRodapeNegrito}</strong>
                </div>
              </div>

              <figure className="text-center mt-2 mb-0 mx-auto logoSecondary">
                <img src={stylization?.logoSecundario} alt="" className="w-100" />
              </figure>
            </S.Box>
          </div>
        </div>
      </div>
    </S.Content>
  );
};

export default Login;
