import 'bootstrap/dist/css/bootstrap.min.css';
import { useEffect, useState } from 'react';

//styles
import { Form } from 'react-bootstrap';
import { CheckCircle } from 'react-bootstrap-icons';
import Button from '../../components/Button';
import PageContainer from '../../components/PageContainer';
import { useUserContext } from '../../context/UserContext';
import { getAutomaticDebitData, saveAccountInfo } from '../../services/automaticDebit';
import { AlertContent } from '../BilletSend/styles';
import { FormGroup, PageTitleContainer, Title } from '../ChangeAddress/styles';

const AutomaticDebit = () => {
  const { userData, loginResponseData, group, quota } = useUserContext();

  const [bank, setBank] = useState<string>('');
  const [account, setAccount] = useState<string>('');
  const [agency, setAgency] = useState<string>('');
  const [digit, setDigit] = useState<string>('');
  const [check, setCheck] = useState<boolean>(false);
  const [showAlert, setShowAlert] = useState<boolean>();

  useEffect(() => {
    if (!userData || !group || !quota) return;

    const func = async () => {
      try {
        const res = await getAutomaticDebitData(group + '', quota);
        if (!res) return;
        setAccount(res.debitoConta);
        setBank(res.debitoBanco);
        setDigit(res.debitoDigito);
        setAgency(res.debitoAgencia);
        setCheck(!!res && res.debitoEmConta);
      } catch (e) {
        console.log(e);
      }
    };
    func();
  }, [userData, group, quota]);

  const handleSave = async () => {
    try {
      const res = await saveAccountInfo(bank, agency, digit, account);
      if (res) setShowAlert(true);
      setTimeout(() => setShowAlert(false), 5000);
    } catch (e) {
      console.log(e);
    }
  };

  const pageContent = () => {
    return (
      <>
        <PageTitleContainer>
          <div className={'d-flex align-center gap-3 mt-4 mt-lg-0'}>
            <Title>Dbito automtico</Title>

            <Form.Check
              type="switch"
              id="custom-switch"
              checked={check}
              onChange={(e: any) => setCheck(e.target.checked)}
            />
          </div>
        </PageTitleContainer>

        {showAlert && (
          <AlertContent>
            <CheckCircle size={22} />
            Alteraes feita com sucesso!
          </AlertContent>
        )}

        <div className="row">
          <div className="col-lg-9">
            <div className="row">
              <div className="col-md-6">
                <FormGroup controlId="formBasicCEP">
                  <Form.Label>Banco</Form.Label>
                  <Form.Control
                    maxLength={3}
                    type="text"
                    placeholder="Banco"
                    required
                    value={bank}
                    onChange={(e: any) => setBank(e.target.value)}
                  />
                </FormGroup>
              </div>
              <div className="col-7 col-md-4">
                <FormGroup controlId="formBasicCEP">
                  <Form.Label>Agncia</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Agncia"
                    required
                    value={agency}
                    onChange={(e: any) => setAgency(e.target.value)}
                  />
                </FormGroup>
              </div>
              <div className="col-5 col-md-2 d-flex align-items-end">
                <FormGroup controlId="formBasicCEP">
                  <Form.Control
                    type="text"
                    placeholder="Dgito"
                    required
                    value={digit}
                    onChange={(e: any) => setDigit(e.target.value)}
                  />
                </FormGroup>
              </div>
            </div>

            <div className="row mb-4">
              <div className="col-md-6">
                <FormGroup controlId="formBasicCEP">
                  <Form.Label>Conta</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Conta"
                    required
                    value={account}
                    onChange={(e: any) => setAccount(e.target.value)}
                  />
                </FormGroup>
              </div>
              <div className="col-md-6 col-lg-4">
                <FormGroup controlId="formBasicCEP">
                  <Form.Label>Utilizao de cheque especial</Form.Label>
                  <div className="mt-1">
                    <Form.Check inline label="Sim" name="group1" type={'radio'} id={`inline-radio-1`} disabled />
                    <Form.Check inline label="No" name="group1" type={'radio'} id={`inline-radio-2`} disabled />
                  </div>
                </FormGroup>
              </div>
            </div>
          </div>
        </div>

        <Button onClick={() => handleSave()}>Salvar</Button>
      </>
    );
  };

  return <>{userData && <div>{pageContent()}</div>}</>;
};

export default AutomaticDebit;
