/**
 * 1 = NOT FOUND;
 * 2 = PERMISSION;
 * 3 = FIELD EMPTY;
 * 4 = FIELD VALUE INVALID;
 * 5 = INVALID ACTION;
 */
const CUSTOM_ERRORS: any = {
  1: (response: any) => {
    response.data.error = 'No encontrado';
    return Promise.resolve(response.data);
  },
  2: (response: any) => {
    response.data.error = `Acesso no autorizado`;
  },
  3: (response: any) => {
    response.data.error = `Existem campos em branco`;
  },
  4: (response: any) => {
    response.data.error = 'Existem campos com dados invlidos';
  },
  5: (response: any) => {
    response.data.error = 'Ao invlida';
  },
  6: (response: any) => {
    response.data.error = 'Acesso no autorizado';

    return Promise.reject(JSON.stringify(response.data));
  },
};

export default CUSTOM_ERRORS;
