import 'bootstrap/dist/css/bootstrap.min.css';
import { useEffect, useRef, useState } from 'react';

//styles
import {
  Accordion,
  Card,
  Dropdown,
  DropdownButton,
  Form,
  InputGroup,
  Overlay,
  Popover,
  useAccordionButton,
} from 'react-bootstrap';
import { CheckCircle, Eye, InfoCircleFill, Percent, Printer, Trash } from 'react-bootstrap-icons';
import { useReactToPrint } from 'react-to-print';
import Button from '../../components/Button';
import { formatMoney } from '../../constants/functions';
import { useUserContext } from '../../context/UserContext';
import { Bid, calculateOffers, getOffers, saveOffers } from '../../services/bidOffer';
import { AlertContent } from '../BilletSend/styles';
import { PageTitleContainer, Title } from '../ChangeAddress/styles';
import { ButtonsContainer } from '../GroupsAvailability/styles';
import {
  AllQuotasOffer,
  BidEditButton,
  CardHead,
  Collumn,
  Forms,
  InputLine,
  Offer,
  OfferForm,
  Tag2,
  Title2,
} from './styles';

// @ts-ignore
function CustomToggle({ children, eventKey, onClick, hierarchy = 'primary', disabled = false }) {
  const decoratedOnClick = useAccordionButton(eventKey, onClick);

  return (
    <Button
      type="button"
      onClick={(e: any) => {
        decoratedOnClick(e);
      }}
      disabled={disabled}
      hierarchy={hierarchy}
    >
      {children}
    </Button>
  );
}

interface BidOption {
  lanceGrupo: string;
  lanceCota: string;
  lanceTipoLance: string;
  lanceOferta: string;
  embutido: boolean;
  percEmbutido: string;
  selectedRule: any;
}

const BidOffer = () => {
  const { userData, loginResponseData, group, quota } = useUserContext();

  const [showAlert, setShowAlert] = useState<boolean>();

  const [view, setView] = useState<'all' | 'form'>('all');

  const [formStep, setFormStep] = useState(0);

  const [offersInitial, setOffersInitial] = useState<Bid[]>();

  const [gruposCotas, setGruposCotas] = useState<any[]>();
  const [contemplation, setContemplation] = useState<any[]>();
  const [offersForm, setOffersForm] = useState<BidOption[]>([]);
  const [allFilters, setAllFilters] = useState('');

  const [revisionOffers, setRevisionOffers] = useState<any[]>([]);

  const [finalOffers, setFinalOffers] = useState<any[]>([]);
  useEffect(() => {
    if (!loginResponseData) return;

    setGruposCotas(
      loginResponseData.consorciados.map((consorciado) => ({
        cota: consorciado.cota,
        grupo: consorciado.grupo,
      }))
    );
  }, [loginResponseData, group, quota]);

  useEffect(() => {
    const func = async () => {
      try {
        if (!gruposCotas) return;
        const res = await getOffers(gruposCotas);
        if (!res) return;
        setOffersInitial(res.lances);
        setOffersForm(
          res.lances.map(
            (lance) =>
              ({
                lanceGrupo: lance.lanceGrupo,
                lanceCota: lance.lanceCota,
                lanceTipoLance: '1',
              } as BidOption)
          )
        );
        setContemplation(res.regras);
      } catch (e) {
        console.log(e);
      }
    };
    func();
  }, [gruposCotas]);

  const [show, setShow] = useState(false);
  const [target, setTarget] = useState(null);
  const ref = useRef(null);
  const printRef = useRef(null);

  const handleTooltip = (event: any) => {
    setShow(!show);
    setTarget(event.target);
  };

  const handleCalculate = async () => {
    try {
      let _offers = [...offersForm];
      _offers = _offers.filter((offer) => {
        return offer.lanceOferta && offer.lanceOferta != '0' && offer.lanceTipoLance;
      });
      const res = await calculateOffers(_offers);
      setRevisionOffers(res);
    } catch (e) {
      console.log(e);
    }
    setFormStep(1);
  };
  const handleSave = async () => {
    try {
      let _offers = [...offersForm];
      _offers = _offers.filter((offer) => {
        return offer.lanceOferta && offer.lanceOferta != '0' && offer.lanceTipoLance;
      });
      const res = await saveOffers(_offers);
      setFinalOffers(res);
    } catch (e) {
      console.log(e);
    }
    setFormStep(2);
  };
  const handleNew = () => {
    const _offers = [...offersForm];

    for (let i = 0; i < offersForm.length; i++) {
      _offers[i].lanceOferta = '0';
    }
    setOffersForm(_offers);

    setView('form');
  };
  const handleEdit = () => {
    const _offers = [...offersForm];
    if (!offersInitial) return;
    for (let i = 0; i < offersForm.length; i++) {
      _offers[i].lanceOferta = Number(offersInitial[i].lanceOferta).toString();
    }
    setOffersForm(_offers);
    setView('form');
  };
  const handleChangeTipoLance = (index: number, lanceTipoLance: string) => {
    const _offers = [...offersForm];
    _offers[index] = {
      ..._offers[index],
      lanceTipoLance,
    };
    setOffersForm(_offers);
  };

  useEffect(() => {
    if (!offersForm || !allFilters) return;
    let _offers = [...offersForm];
    _offers = _offers.map((offer) => {
      return {
        ...offer,
        lanceTipoLance: allFilters,
      };
    });
    setOffersForm(_offers);
  }, [allFilters]);
  const handlePrint = useReactToPrint({
    content: () => printRef?.current,
  });
  const renderFilter = (data: string) => {
    if (data === '1') {
      return 'Parcelas';
    }
    if (data === '2') {
      return '%';
    }
    if (data === '3') {
      return 'R$';
    }
  };

  const pageContent = () => {
    return (
      <>
        <PageTitleContainer>
          <Title>Oferta de lance</Title>
          <ButtonsContainer>
            {view === 'all' ? (
              <>
                <Button onClick={() => handleNew()}>Novo Lance</Button>

                <BidEditButton hierarchy={'secondary'} onClick={() => handleEdit()}>
                  Editar Lance
                </BidEditButton>
              </>
            ) : (
              <>
                <BidEditButton hierarchy={'secondary'} onClick={() => setView('all')}>
                  Ver todos os Lances
                </BidEditButton>
              </>
            )}
          </ButtonsContainer>
        </PageTitleContainer>

        {showAlert && (
          <AlertContent>
            <CheckCircle size={22} />
            Alteraes feita com sucesso!
          </AlertContent>
        )}

        <div className={view === 'all' ? 'd-flex flex-column gap-3' : 'd-none'}>
          {offersInitial &&
            offersInitial
              .filter((offer) => Number(offer.lanceOferta) !== 0)
              .map((offer) => (
                <Offer>
                  <div className={'d-flex flex-column'}>
                    <div className={'title'}>Grupo/ Quota</div>
                    <div className={'mainData'}>
                      {offer.lanceGrupo} / {offer.lanceCota}
                    </div>
                  </div>

                  <div className="d-flex gap-4 justify-content-between flex-wrap">
                    <div className={'d-flex flex-column'}>
                      <div className={'title'}>Data Assembleia</div>
                      <div className={'data'}>{offer.lanceDataAssembleia}</div>
                    </div>

                    <div className={'d-flex flex-column'}>
                      <div className={'title'}>Oferta</div>
                      <div className={'data'}>{Number(offer.lanceOferta)}%</div>
                    </div>
                  </div>
                </Offer>
              ))}
        </div>

        <div className={view === 'form' ? 'd-block' : 'd-none'}>
          <Forms>
            <Accordion defaultActiveKey="0">
              <Card>
                <CardHead className={formStep >= 1 ? 'finished' : formStep === 0 ? 'active' : ''}>
                  <Percent size={24} />
                  Criar novo Lance
                </CardHead>

                <Accordion.Collapse eventKey="0">
                  <Card.Body>
                    <div className={'row'}>
                      <div className="col-lg-5 col-xl-3">
                        <AllQuotasOffer>
                          <h3>Lance para todas as cotas</h3>
                          <InputGroup>
                            <Form.Control
                              aria-label="Text input with dropdown button"
                              placeholder="R$ 0,00"
                              onChange={(e) => {
                                let _offers = [...offersForm];
                                _offers = _offers.map((offer) => {
                                  return {
                                    ...offer,
                                    lanceOferta: e.target.value,
                                  };
                                });
                                setOffersForm(_offers);
                              }}
                            />

                            <DropdownButton
                              variant="outline-secondary"
                              title={renderFilter(allFilters)}
                              id="input-group-dropdo6"
                              align="end"
                            >
                              <Dropdown.Item onClick={() => setAllFilters('1')}>Parcelas</Dropdown.Item>
                              <Dropdown.Item onClick={() => setAllFilters('2')}>%</Dropdown.Item>
                              <Dropdown.Item onClick={() => setAllFilters('3')}>R$</Dropdown.Item>
                            </DropdownButton>
                          </InputGroup>

                          <Form.Select
                            aria-label="Default select example"
                            onChange={(e) => {
                              let _offers = [...offersForm];
                              let selectedRule = contemplation?.find((rule) => rule.forma === e.target.value);
                              _offers = _offers.map((offer) => {
                                return {
                                  ...offer,
                                  selectedRule: {
                                    forma: selectedRule.forma,
                                    codigo: selectedRule.codigo,
                                    prioridade: selectedRule.prioridade,
                                  },
                                };
                              });
                              setOffersForm(_offers);
                            }}
                          >
                            {contemplation &&
                              contemplation
                                .filter((cont) => cont.forma !== '00')
                                .map((contemplationWay) => (
                                  <option value={contemplationWay.forma}>{contemplationWay.descricao} </option>
                                ))}
                          </Form.Select>

                          <Form.Check
                            type={'checkbox'}
                            label={`Lance embutido`}
                            // checked={offer.embutido}
                            // onChange={(e) => {
                            //   const _offers = [...offersForm];
                            //   _offers[index] = {
                            //     ..._offers[index],
                            //     embutido: e.target.checked,
                            //   };
                            //   setOffersForm(_offers);
                            // }}
                          />
                        </AllQuotasOffer>
                        <div className={'mt-4'}>
                          <CustomToggle
                            onClick={() => handleCalculate()}
                            eventKey={'1'}
                            disabled={offersForm.every(
                              (offer) =>
                                ['', '0'].includes(offer.lanceOferta) ||
                                !offer ||
                                !offer.selectedRule ||
                                offer.selectedRule.forma == '99'
                            )}
                          >
                            Confirmar
                          </CustomToggle>
                        </div>
                      </div>

                      <div className="mt-4 mt-lg-0 col-lg-7 col-xl-9">
                        <div className={'d-flex flex-column gap-3 w-100'}>
                          {offersForm?.map((offer, index) => (
                            <OfferForm key={offer.lanceCota}>
                              <h3 className={'fw-bold'}>
                                {offer.lanceGrupo} / {offer.lanceCota}
                              </h3>
                              <InputLine>
                                <Collumn>
                                  <Form.Select
                                    aria-label="Default select example"
                                    value={offer?.selectedRule?.forma}
                                    onChange={(e) => {
                                      const _offers = [...offersForm];
                                      let selectedRule = contemplation?.find((rule) => rule.forma === e.target.value);
                                      _offers[index] = {
                                        ..._offers[index],
                                        selectedRule: {
                                          forma: selectedRule.forma,
                                          codigo: selectedRule.codigo,
                                          prioridade: selectedRule.prioridade,
                                        },
                                      };
                                      setOffersForm(_offers);
                                    }}
                                  >
                                    {contemplation &&
                                      contemplation
                                        .filter((cont) => cont.forma !== '00')
                                        .map((contemplationWay, index) => (
                                          <option value={contemplationWay.forma}>{contemplationWay.descricao}</option>
                                        ))}
                                  </Form.Select>
                                  <Form.Check
                                    type={'checkbox'}
                                    label={`Lance embutido`}
                                    checked={offer.embutido}
                                    onChange={(e) => {
                                      const _offers = [...offersForm];
                                      _offers[index] = {
                                        ..._offers[index],
                                        embutido: e.target.checked,
                                      };
                                      setOffersForm(_offers);
                                    }}
                                  />
                                </Collumn>
                                <Collumn>
                                  <InputGroup>
                                    <Form.Control
                                      aria-label="Text input with dropdown button"
                                      value={offer.lanceOferta}
                                      type="number"
                                      onChange={(e) => {
                                        const _offers = [...offersForm];
                                        _offers[index] = {
                                          ..._offers[index],
                                          lanceOferta: e.target.value,
                                        };
                                        setOffersForm(_offers);
                                      }}
                                    />

                                    <DropdownButton
                                      variant="outline-secondary"
                                      title={renderFilter(offer.lanceTipoLance)}
                                      id="input-group-dropdown-2"
                                      align="end"
                                    >
                                      <Dropdown.Item
                                        onClick={() => {
                                          handleChangeTipoLance(index, '1');
                                        }}
                                      >
                                        Parcelas
                                      </Dropdown.Item>
                                      <Dropdown.Item
                                        onClick={() => {
                                          handleChangeTipoLance(index, '2');
                                        }}
                                      >
                                        %
                                      </Dropdown.Item>
                                      <Dropdown.Item
                                        onClick={() => {
                                          handleChangeTipoLance(index, '3');
                                        }}
                                      >
                                        R$
                                      </Dropdown.Item>
                                    </DropdownButton>
                                  </InputGroup>
                                  <div className={'grayText'}>
                                    Lance Mximo:{' '}
                                    {Number(offersInitial ? offersInitial[index].lanceQtdeMaximoGrupo : 0)}% <br />
                                    Lance Mnimo:{' '}
                                    {Number(offersInitial ? offersInitial[index].lanceQtdeMinimoGrupo : 0)}%
                                  </div>
                                </Collumn>
                                <Collumn className="align-items-end align-items-md-center">
                                  <Trash
                                    size={24}
                                    className={'trashIcon'}
                                    onClick={() => {
                                      const _offers = [...offersForm];
                                      _offers[index] = {
                                        ..._offers[index],
                                        lanceOferta: '0',
                                      };
                                      setOffersForm(_offers);
                                    }}
                                  />
                                </Collumn>
                              </InputLine>
                            </OfferForm>
                          ))}
                        </div>
                      </div>
                    </div>
                  </Card.Body>
                </Accordion.Collapse>
              </Card>

              <Card>
                <CardHead className={formStep >= 2 ? 'finished' : formStep === 1 ? 'active' : ''}>
                  <Eye />
                  Reviso do Lance
                  {/*<CustomToggle eventKey='1'>Click me!</CustomToggle>*/}
                </CardHead>
                <Accordion.Collapse eventKey="1">
                  <div className={'w-100 d-flex flex-column gap-3'}>
                    {revisionOffers &&
                      revisionOffers.map((offer) => (
                        <Offer>
                          <Collumn>
                            <div className={'mainData'}>
                              {offer.lanceGrupo} / {offer.lanceCota}{' '}
                            </div>
                            {offersForm.findIndex(
                              (of) =>
                                of.embutido && of.lanceGrupo === offer.lanceGrupo && of.lanceCota === offer.lanceCota
                            ) > -1 && <Tag2>Lance Embutido</Tag2>}
                            <Tag2>
                              {offersForm.find(
                                (of) => of.lanceGrupo === offer.lanceGrupo && of.lanceCota === offer.lanceCota
                              )?.selectedRule?.forma === '11'
                                ? 'Lance Livre'
                                : 'Lance Limitado'}
                            </Tag2>
                          </Collumn>

                          <Collumn>
                            <div className={'title'}>Oferta</div>
                            <div className={'data'}>
                              {!isNaN(Number(offer.lanceOfertado.split('Porcento')[0]))
                                ? Number(offer.lanceOfertado.split('Porcento')[0])
                                : ''}
                              %
                            </div>
                          </Collumn>

                          <Collumn>
                            <div className={'title'}>
                              Valor Total do Lance <InfoCircleFill onClick={handleTooltip} />
                              <div ref={ref} className={'Valor do Lance a Pagar + Valor do Lance Embutido'}>
                                <Overlay
                                  show={show}
                                  target={target}
                                  placement="top"
                                  container={ref}
                                  containerPadding={20}
                                >
                                  <Popover id="popover-contained">
                                    <Popover.Body>Valor do Lance a Pagar + Valor do Lance Embutido</Popover.Body>
                                  </Popover>
                                </Overlay>
                              </div>
                            </div>
                            <div className={'data'}>{formatMoney(offer.lanceValorTotal)}</div>
                          </Collumn>

                          <Collumn>
                            <div className={'title'}>Crdito</div>
                            <div className={'data'}>{formatMoney(offer['16'])}</div>
                          </Collumn>

                          {/*<Collumn>*/}
                          {/*  <div className={'title'}>*/}
                          {/*    Valor do Lance Embutido <InfoCircleFill onClick={handleTooltip} />*/}
                          {/*    <div ref={ref} className={'Valor do Lance a Pagar + Valor do Lance Embutido'}>*/}
                          {/*      <Overlay*/}
                          {/*        show={show}*/}
                          {/*        target={target}*/}
                          {/*        placement="top"*/}
                          {/*        container={ref}*/}
                          {/*        containerPadding={20}*/}
                          {/*      >*/}
                          {/*        <Popover id="popover-contained">*/}
                          {/*          <Popover.Body>*/}
                          {/*            Valor do Lance Embutido a ser descontado do Valor do Crdito quando ocorrer a*/}
                          {/*            confirmao da Contemplao por lance*/}
                          {/*          </Popover.Body>*/}
                          {/*        </Popover>*/}
                          {/*      </Overlay>*/}
                          {/*    </div>*/}
                          {/*  </div>*/}
                          {/*  <div className={'data'}>R$ 0,00</div>*/}
                          {/*</Collumn>*/}
                        </Offer>
                      ))}

                    <div>
                      <div>
                        <Title2>
                          Valor Total dos Lances Ofertados
                          <InfoCircleFill onClick={handleTooltip} />
                          <div ref={ref} className={'Valor do Lance a Pagar + Valor do Lance Embutido'}>
                            <Overlay show={show} target={target} placement="top" container={ref} containerPadding={20}>
                              <Popover id="popover-contained">
                                <Popover.Body>
                                  Valor do Lance Embutido a ser descontado do Valor do Crdito quando ocorrer a
                                  confirmao da Contemplao por lance
                                </Popover.Body>
                              </Popover>
                            </Overlay>
                          </div>
                          <h2>
                            {formatMoney(
                              revisionOffers.reduce(
                                (acc, cur) => Number(cur.lanceValorTotal.replace(/\./g, '').replace(',', '.')) + acc,
                                0
                              )
                            )}
                          </h2>
                        </Title2>
                        <div className={'d-flex gap-3 '}>
                          <CustomToggle eventKey={'0'} onClick={() => setFormStep(0)} hierarchy={'secondary'}>
                            Voltar
                          </CustomToggle>
                          <CustomToggle eventKey={'2'} onClick={() => handleSave()}>
                            Salvar
                          </CustomToggle>
                        </div>
                      </div>
                    </div>
                  </div>
                </Accordion.Collapse>
              </Card>

              <Card>
                <CardHead className={formStep >= 3 ? 'finished' : formStep === 2 ? 'active' : ''}>
                  <Percent size={24}></Percent>
                  Confirmao do Lance
                  {/*<CustomToggle eventKey='0'>Click me!</CustomToggle>*/}
                </CardHead>
                <Accordion.Collapse eventKey="2">
                  <Card.Body ref={printRef}>
                    <div className={'w-100 d-flex flex-column gap-3'}>
                      {finalOffers.map((offer) => (
                        <Offer>
                          <Collumn>
                            <div className={'mainData'}>
                              {offer.lanceGrupo} / {offer.lanceCota}{' '}
                            </div>
                            {offersForm.findIndex(
                              (of) =>
                                of.embutido && of.lanceGrupo === offer.lanceGrupo && of.lanceCota === offer.lanceCota
                            ) > -1 && <Tag2>Lance Embutido</Tag2>}
                            <Tag2>
                              {offersForm.find(
                                (of) => of.lanceGrupo === offer.lanceGrupo && of.lanceCota === offer.lanceCota
                              )?.selectedRule?.forma === '11'
                                ? 'Lance Livre'
                                : 'Lance Limitado'}
                            </Tag2>
                          </Collumn>

                          <Collumn>
                            <div className={'title'}>Data Assembleia</div>
                            <div className={'data'}>{offer.lanceDataAssembleia}</div>
                          </Collumn>

                          <Collumn>
                            <div className={'title'}>Oferta</div>
                            <div className={'data'}>
                              {!isNaN(Number(offer.lanceOfertado.split('Porcento')[0]))
                                ? Number(offer.lanceOfertado.split('Porcento')[0])
                                : ''}
                              %
                            </div>
                          </Collumn>
                          <Collumn>
                            <div className={'title'}>Valor Total do Lance</div>
                            <div className={'data'}>
                              {' '}
                              {formatMoney(Number(offer.lanceValorTotal.replace(/\./g, '').replace(',', '.')))}
                            </div>
                          </Collumn>
                          <Collumn>
                            <div className={'title'}>Senha para consulta</div>
                            <div className={'data'}>{offer.senha}</div>
                          </Collumn>
                        </Offer>
                      ))}

                      <div>
                        <Button onClick={() => handlePrint()} hideOnPrint={true}>
                          <Printer></Printer> Imprimir
                        </Button>
                      </div>
                    </div>
                  </Card.Body>
                </Accordion.Collapse>
              </Card>
            </Accordion>
          </Forms>
        </div>
      </>
    );
  };

  return <>{userData && <div>{pageContent()}</div>}</>;
};

export default BidOffer;
