import React, { createContext, Dispatch, SetStateAction, ReactNode, useState, useEffect, useContext } from 'react';
import { UserContext } from './UserContext';

interface ChildrenProps {
  children: ReactNode;
}

interface Context {
  openMenu: boolean;
  setOpenMenu: Dispatch<SetStateAction<boolean>>;
}

const initialContext: Context = {
  openMenu: false,
  setOpenMenu: () => false,
};

export const NavBarMenuContext = createContext<Context>(initialContext);

export const NavBarMenuProvider = ({ children }: ChildrenProps) => {
  const [openMenu, setOpenMenu] = useState<boolean>(false);

  return <NavBarMenuContext.Provider value={{ openMenu, setOpenMenu }}>{children}</NavBarMenuContext.Provider>;
};
export const useNavbarContext = () => {
  const context = useContext(NavBarMenuContext);
  return context;
};
