import { Info, TitleBox } from './styles';

interface CardInfoProps {
  title: string;
  info?: string;
}

export function CardInfo(props: CardInfoProps) {
  return (
    <>
      <TitleBox>
        <h2>{props.title}</h2>
      </TitleBox>

      {props.info && <Info>{props.info}</Info>}
    </>
  );
}
